.Button {
    display: inline-block;
    padding: 10px 20px;
    font-size: 1rem;
    font-weight: bold;
    text-transform: uppercase;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
  
.classicBtn{
    color: white;
    background-color: #4BB4DE;
}

.classicBtn:hover{
    background-color: #345DA7;
}

.classicBtn:disabled {
    cursor: default;
    background-color: #a1cada;
    opacity: 0.7;
  }

.whiteBtn{
    color: #283D5A;
    background-color: #ffffff;
}

.whiteBtn:hover{
    color: white;
    background-color: #345DA7;
}

.darkBtn{
    padding: 5px 10px;
    color: #ffffff;
    background-color: #345DA7;
}

.darkBtn:hover{
    color: #345DA7;
    background-color: #ffffff;
}

